.button_green {
	width: rem(224);
	height: rem(46);
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: rem(13);
	background: linear-gradient(180deg, #27E78C 0%, #4DC18A 100%);

	&:hover {
		background: linear-gradient(180deg, #4DC18A 0%, #27E78C 100%);
	}

	span {
		font-weight: bold;
		color: white;
	}
}

.button_green_border {
	position: relative;
	width: rem(224);
	height: rem(46);
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: rem(13);
	background: linear-gradient(180deg, #27E78C 0%, #4DC18A 100%);

	&::before {
		content: "";
		position: absolute;
		width: calc(100% - 6px);
		height: calc(100% - 6px);
		top: 3px;
		left: 3px;
		background-color: white;
		border-radius: rem(11);
		transition: 250ms ease;
	}
	
	&:hover {
		&::before {
			opacity: 0;
		}

		span {
			color: white;
		}
	}
	
	span {
		position: relative;
		color: #4DC18A;
		z-index: 2;
	}
}

.text-red {
	color: #DA3A3A;
}