.wrap_box {
	background-color: #E5E5E5;
	padding: rem(42) rem(40) rem(30) rem(40);

	display: flex;
	flex-direction: column;

	@include min768 {
		padding: rem(21) rem(20) rem(15) rem(20);
	}

	.wrap_header {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
		margin-bottom: rem(22);

		@include min1200 {
			flex-direction: column;
			align-items: flex-start;
		}

		.title  {
			font-weight: bold;
			// color: #4F4F4F;
			font-size: rem(36);

			@include min1200 {
				margin-bottom: rem(20);
			}

			@include min1050 {
				font-size: rem(24);
			}
		}

		&__links {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			@include min1200 {
				flex-direction: column;
			}

			.button_green {
				margin-right: rem(16);

				@include min1200 {
					margin-right: 0;
					margin-bottom: rem(16);
				}
			}

			.button_green_border {
				margin-right: rem(16);

				@include min1200 {
					margin-right: 0;
					margin-bottom: rem(16);
				}

				&:last-child {
					margin-right: 0;

					@include min1200 {
						margin-bottom: 0;
					}
				}
			}
		}
	}

	.wrap_content {
		border-radius: rem(8);
		background-color: white;
		padding: rem(26) rem(16);

		@include min768 {
			padding: rem(13) rem(8);
		}

		&__grafic {
			position: relative;
			// margin-top: rem(20);
			// margin-bottom: rem(80);
			margin: rem(32) rem(22) rem(80) rem(22);

			@include min768 {
				margin: rem(16) rem(11) rem(40) rem(11);
			}

			.line {
				position: relative;
				z-index: 2;
				background-color: #DFDFDF;
				box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
				border-radius: rem(15);
				height: rem(26);

				&_active {
					height: 100%;
					border-radius: rem(15);
					background: linear-gradient(90deg, #F2F545 0%, #4DC18A 96.26%);
					box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
					position: relative;

					&::before {
						content: "";
						position: absolute;
						top: rem(2);
						right: rem(-2);
						background: linear-gradient(180deg, #FFFFFF 0%, #E8E8E8 100%);
						box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

						width: rem(22);
						height: rem(22);
						border-radius: 50%;
					}
				}

				
			}

			.steps {
					display: flex;
					position: absolute;
					top: rem(-36);
					left: 0;
					height: rem(100);
					width: 100%;

					> div {
						position: relative;
						width: 6.6%;
						height: rem(48);

						display: flex;
						flex-direction: column-reverse;
						align-items: center;

						@include min768 {
							width: 6.5%;
						}

						@include min540 {
							opacity: 0;
						}

						&:nth-child(2n) {
							align-self: flex-end;
							flex-direction: column;
						}

						span {
							// position: absolute;
							// top: 0;
							// left: 50%;
							// transform: translateX(-50%);
							font-size: rem(12);
							color: #C6C6C6;
							white-space: nowrap;

							@include min768 {
								font-size: rem(8);
							}
						}

						&::before {
							content: "";
							height: rem(26);
							width: rem(2);
							display: block;
							background-color: #ABABAB;
						}
					}

					> div.active {
						@include min540 {
							opacity: 1;
						}
						&::before {
							background-color: #459315;
						}
						span {
							background-color: #459315;
							padding: rem(3) rem(10);
							border-radius: rem(17);
							color: white;
							font-weight: bold;
						}
					}
				}
		}

		&__row {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			padding: 0 rem(28);

			@include min768 {
				padding: 0 rem(14);
			}

			.left {
				flex: 0 0 calc(65% - 40px);
				max-width: calc(65% - 40px);

				display: flex;
				flex-wrap: wrap;

				@include min1440 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-bottom: rem(20);
				}

				.wrap_table {
					display: flex;
					flex-direction: column;
					margin-bottom: rem(28);

					&__header {
						margin-bottom: rem(22);
						display: flex;
						align-items: center;
						justify-content: space-between;

						@include min540 {
							flex-direction: column;
							align-items: flex-start;
						}

						.title {
							font-size: rem(24);
							font-weight: bold;
							color: #4F4F4F;

							@include min1050 {
								font-size: rem(18);
							}

							@include min540 {
								margin-bottom: rem(14);
							}
						}

						.button {
							padding: rem(10) rem(22);
							background: linear-gradient(180deg, #27E78C 0%, #4DC18A 100%);
							box-shadow: 0px 5px 13px rgba(163, 163, 163, 0.3);
							border-radius: 34px;

							@include min1050 {
								padding: rem(6) rem(16);
							}

							&:hover {
								background: linear-gradient(180deg, #4DC18A 0%, #27E78C 100%);
							}

							span {
								font-size: rem(18);
								color: white;
								font-weight: bold;

								@include min1050 {
									font-size: rem(14);
								}
							}
						}
					}

					&__box {
						background-color: #F5F5F5;
						border-radius: rem(19);
						padding: rem(26) rem(32) rem(55) rem(32);

						@include min1050 {
							padding: rem(13) rem(16) rem(27) rem(16);
							
							overflow-x: auto;
						}

						.items {
							display: flex;
							flex-direction: column;
							
							@include min768 {
								width: rem(1020);
							}

							.item {
								display: flex;
								flex-wrap: wrap;
								justify-content: space-between;

								&:first-child {
									margin-bottom: rem(15);
								}

								&:not(:first-child) {
									padding: rem(12) 0;
									border-bottom: rem(2) solid white;
								}

								b, span {
									flex: 0 0 14.2%;
									max-width: 14.2%;
								}

								b {
									font-size: rem(13);
								}
								font-weight: 300;
								font-size: rem(12);
							}
						}

						.list {
							display: flex;
							flex-direction: column;

							li {
								display: flex;
								flex-direction: column;
								margin-bottom: rem(20);

								&:last-child {
									margin-bottom: 0;
								}

								span {
									&:first-child {
										margin-bottom: rem(8);
										color: #4F4F4F;
									}
									&:last-child {
										font-weight: bold;
										font-size: rem(18);

										@include min {
											font-size: rem(16);
										}
									}
								}
							}
						}
					}
				}
			}

			.right {
				flex: 0 0 35%;
				max-width: 35%;

				display: flex;
				flex-direction: column;

				@include min1440 {
					flex: 0 0 100%;
					max-width: 100%;
				}

				.message_block {
					display: flex;
					flex-direction: column;

					&__header {
						margin-bottom: rem(28);

						.title {
							font-size: rem(24);
							color: #4F4F4F;

							@include min1050 {
								font-size: rem(18);
							}
						}
					}

					&__wrap {
						position: relative;
						height: rem(603);

						background-color: #F5F5F5;
						border-radius: rem(19);
						padding: rem(30) rem(18);

						display: flex;
						flex-direction: column;
						// height: 100%;

						.data {
							position: absolute;
							top: 45%;
							left: 50%;
							transform: translateX(-50%);

							color: #808080;

							@include min540 {
								font-size: rem(12);
							}
						}

						.message {
							margin-top: auto;
							display: flex;
							flex-direction: column;
							justify-content: space-between;

							&__left, &__right {
								margin-bottom: rem(10);
								border-radius: rem(11);
								padding: rem(8) rem(11);
								color: white;
								text-align: right;
								width: 45%;

								@include min540 {
									font-size: rem(12);
								}
							}

							&__left {
								align-self: flex-start;
								background-color: #29C079;
								
							}
							&__right {
								align-self: flex-end;
								background-color: #8DA499;
							}
						}

						.fiend_set {
							// margin-top: auto;
							position: relative;
							height: rem(47);
							border-radius: rem(11);
							background-color: white;

							.field {
								background-color: transparent;
								font-size: rem(14);
								color: #808080;
								height: 100%;
								width: 100%;

								padding: rem(2) rem(150) rem(2) rem(10);

								@include min540 {
									padding: rem(2) rem(100) rem(2) rem(10);
								}
							}

							.button {
								position: absolute;
								top: 0;
								right: 0;
								height: 100%;
								background: linear-gradient(180deg, #27E78C 0%, #4DC18A 100%);
								border-radius: rem(11);
								padding: 0 rem(19);

								font-size: rem(18);
								color: white;
								font-weight: bold;

								&:hover {
									background: linear-gradient(180deg, #4DC18A 0%, #27E78C 100%);
								}

								@include min540 {
									padding: 0 rem(14);
									font-size: rem(12);
								}
							}
						}
					}
				}
			}
		}
	}

}

.fx-1 {
	flex: 0 0 100%;
	max-width: 100%;
}

.fx-5 {
	flex: 0 0 calc((100% - 40px) / 2);
	max-width: calc((100% - 40px) / 2);
	margin-right: rem(40);

	&:last-child {
		margin-right: 0;
	}

	@include min768 {
		flex: 0 0 100%;
		max-width: 100%;
		margin-right: 0;
	}
}